/**
 * This module manage all graphical diagrams of process map
 *
 * @author Edward J Morales
 */
bizagi.workportal.widgets.widget.extend("bizagi.workportal.widgets.project.processMap", {}, {

  /**
                                                                                               * Constructor
                                                                                               * @param workportalFacade instance of facade, mandatory
                                                                                               * @param dataService instance of service tier, mandatory
                                                                                               * @param params extra params, its not mandatory
                                                                                               */
  init: function init(workportalFacade, dataService, params) {
    var self = this;

    // Call parent instance
    self._super(workportalFacade, dataService, params);
    self.instanceBizagiProcessMap = new bizagi.processMap(this);

    //Load templates
    self.loadTemplates({
      "project-processmap": bizagi.getTemplate("bizagi.workportal.desktop.widget.project.processMap").concat("#project-processmap-wrapper"),
      "project-process-diagram": bizagi.getTemplate("bizagi.workportal.desktop.widget.project.processMap").concat("#project-processmap-process-diagram"),
      "project-activity-map": bizagi.getTemplate("bizagi.workportal.desktop.widget.project.processMap").concat("#project-processmap-activity-map"),
      "project-subprocess": bizagi.getTemplate("bizagi.workportal.desktop.widget.project.processMap").concat("#project-processmap-subprocess") });

  },

  /**
      * Render a content of the module
      * @return {string} html
      */
  renderContent: function renderContent() {
    var self = this;
    var template = self.instanceBizagiProcessMap.renderContent();
    self.content = template.render(self.viewOptions);
    return self.content;
  },

  /**
      * Set binding and set data
      */
  postRender: function postRender() {
    var self = this;
    self.instanceBizagiProcessMap.postRender();
  },

  /**
     *  Binds events to handlers
     */
  configureHandlers: function configureHandlers() {
    var self = this;
    self.instanceBizagiProcessMap.configureHandlers();
  },

  /**
      * Process diagram handler
      */
  onProcessDiagram: function onProcessDiagram() {
    var self = this;
    self.instanceBizagiProcessMap.onProcessDiagram();
  },

  /**
      * Activity map handler
      */
  onActivityMap: function onActivityMap() {
    var self = this;
    self.instanceBizagiProcessMap.onActivityMap();
  },

  /**
      * Subprocess handler
      */
  onSupprocess: function onSupprocess() {
    var self = this;
    self.instanceBizagiProcessMap.onSupprocess();
  },

  /**
      * Manage navigation between the 3 diagrams
      * @param template
      * @param params
      */
  navigationManager: function navigationManager(template) {
    var self = this;
    var response = self.instanceBizagiProcessMap.navigationManager(template);
    response.container.append(response.template.render(response.viewOptions));
  },

  /**
      * Render the process diagram in the process diagram container
      */
  renderProcessDiagram: function renderProcessDiagram() {
    var self = this;
    self.instanceBizagiProcessMap.renderProcessDiagram();
  },

  resize: function resize() {
    var self = this;
    self.instanceBizagiProcessMap.resize();
  },
  /**
      * Set plugin activity map, draw sequence map
      */
  renderActivityMap: function renderActivityMap() {
    var self = this;
    self.instanceBizagiProcessMap.renderActivityMap();
  },

  renderSubProcessMap: function renderSubProcessMap() {
    var self = this;
    self.instanceBizagiProcessMap.renderSubProcessMap();
  },

  /**
      * Append the activity map zoom
      */
  appendActivityMapZoom: function appendActivityMapZoom() {
    var self = this;
    self.instanceBizagiProcessMap.appendActivityMapZoom();
  },

  /**
      * Append the process map zoom
      */
  appendProcessMapZoom: function appendProcessMapZoom() {
    var self = this;
    self.instanceBizagiProcessMap.appendProcessMapZoom();
  },

  /**
      * Append the subprocess zoom
      */
  appendSubprocessZoom: function appendSubprocessZoom() {
    var self = this;
    self.instanceBizagiProcessMap.appendSubprocessZoom();
  },
  /**
      * Append the subprocess drag and scroll
      */
  appendSubprocessDragScroll: function appendSubprocessDragScroll() {
    var self = this;
    self.instanceBizagiProcessMap.appendSubprocessDragScroll();

  },

  /**
      * binds the tooltip to each activity in activity map
      */
  bindTooltip: function bindTooltip(tooltipParams) {
    var self = this;
    self.instanceBizagiProcessMap.bindTooltip(tooltipParams);
  } });


bizagi.injector.register('bizagi.workportal.widgets.project.processMap', ['workportalFacade', 'dataService', bizagi.workportal.widgets.project.processMap]);